        width: 20px !important;
    }

}
#mobileMenu {
    display: block;
    position: fixed;
    z-index: 3;
    top: 54px;
    height: 100%;
    width: 100%;
    left: -100%;
    background-color: #fff;
    transition: all 200ms cubic-bezier(0.41, 0.93, 0.59, 0.96);
}

#mobileMenu a {
    font-family: 'Montserrat', 'NUNITO', 'Ubuntu', 'Raleway', 'Karla', sans-serif;
    color: black;
    text-decoration: none;
    letter-spacing: 2px;
}

#mobileMenu a:focus {
    color: black;
    border-bottom: 4px solid #fab201;
    transition: ease-in;
    transition-duration: 100ms;
}

#mobileMenu a:hover {
    color: black;
    border-bottom: 4px solid #fab201;
    transition: ease-in;
    transition-duration: 100ms;
}

#mobileMenu .e-mobileMenu {
    opacity: 0;
}

#mobileMenu .e-mobileMenu span {
    width: 100%;
    display: inline-block;
    text-decoration: none;
    color: #000;
    font-weight: 700;
    opacity: 0;
    transition: all 400ms cubic-bezier(0.41, 0.93, 0.59, 0.96);
    transition-delay: 200ms;
    transform: translateX(-50px);
    cursor: pointer;
}

#mobileMenu .e-link-sectionWelcome a {
    color: #0098ff;
}

.menu-open {
    left: 0 !important;
}

.menu-open .e-mobileMenu {
    opacity: 1 !important;
    position: absolute;
    top: 20vh;
    width: 80%;
    height: 300px;
    line-height: 80px;
    left: 20px;
    font-family: "Karla", serif;
    font-size: 20px;
}

.menu-open .e-mobileMenu span {
    opacity: 1 !important;
    transform: translateX(0) !important;
}


@media only screen and (max-width: 850px) {
    #mobileMenu {
        display: block;
    }
}

@media only screen and (max-width: 480px) {
    #mobileMenu {
        display: block;
    }
}
